import { lazy } from 'react'
// import { Redirect } from 'react-router-dom'

const PagesRoutes = [
  {
    path: '/login',
    component: lazy(() => import('../../views/pages/authentication/Login')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  },
  {
    path: '/register',
    component: lazy(() => import('../../views/pages/authentication/Register')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  },
  {
    path: '/forgot-password',
    component: lazy(() => import('../../views/pages/authentication/ForgotPassword')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  },
  {
    path: '/edit-user/:id',
    component: lazy(() => import('../../views/user/employee/Edit')),
    meta: {
      navLink: '/edit-user'
    }
  },
  {
    path: '/edit-customer/:id',
    component: lazy(() => import('../../views/user/customer/Edit')),
    meta: {
      navLink: '/edit-customer'
    }
  },
  {
    path: '/product/add-product',
    component: lazy(() => import('../../views/product/Add'))
    // meta: {
    //   authRoute: true
    // }
  }
]

export default PagesRoutes
