import { lazy } from 'react'

const DashboardRoutes = [

  {
    path: '/dashboard',
    component: lazy(() => import('../../views/dashboard')),
    exact: true
  },
  {
    path: '/user/employee',
    component: lazy(() => import('../../views/user/employee')),
    exact:true
  },
  {
    path: '/user/customer',
    component: lazy(() => import('../../views/user/customer')),
    exact:true
  },
  {
    path: '/category',
    component: lazy(() => import('../../views/category')),
    exact:true
  },
  {
    path: '/product',
    component: lazy(() => import('../../views/product')),
    exact:true
  },
  {
    path: '/order',
    component: lazy(() => import('../../views/order')),
    exact:true
  }
]

export default DashboardRoutes
